import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ApplicationDataService } from 'src/app/Services/application-data.service';
import { ProjectService } from 'src/app/Services/project.service';
import { ToasterNotificatonService } from 'src/app/Services/toaster.notificaton.service';

@Component({
  selector: 'app-application-layer',
  templateUrl: './application-layer.component.html',
  styleUrls: ['./application-layer.component.css']
})
export class ApplicationLayerComponent implements OnInit {
  projectId: number;
  applicationId:Number;
  appLayerAdded:Boolean = false
  constructor( private fb: FormBuilder,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private dataService: ApplicationDataService) { }

  ngOnInit(): void {
    this.activatedRoute.parent.params.subscribe((params) => {
      console.log("see the params", params.id);
      this.projectId = params.id;
    });

    this.activatedRoute.queryParams.subscribe((params) => {
      this.applicationId = params["applicationId"];
      console.log("see application id", this.applicationId);
    });
    if (this.applicationId !== undefined) {
      console.log("see application id inside if", this.applicationId);
      this.getApplicationById();
    }
  }

  getApplicationById() {
    this.projectService
      .getApplicationById(this.applicationId)
      .subscribe((data: any) => {
        if (data.responseObject.applicationLayerList.length>0) {
          this.appLayerAdded = true;
          this.router.navigate(
            [`/projectdetailsDashboard/${this.projectId}/overViewDisplayApplicationLayer`],
            {
              relativeTo: this.activatedRoute,
              queryParams: { applicationId: `${ this.applicationId}` },
              queryParamsHandling: 'merge',
              replaceUrl: true
            }
            
          );
        }
      });
  }

}
