import { HttpErrorResponse } from "@angular/common/http";
import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { Subscription } from "rxjs";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";
import * as XLSX from "xlsx";
import { projectData } from "../project-list-table/project-list-table.component";
import Swal from "sweetalert2";
import * as moment from "moment";
import {
  ChartComponent,
  ApexAxisChartSeries,
  ApexChart,
  ApexPlotOptions,
  ApexXAxis,
  ApexFill,
  ApexDataLabels,
  ApexYAxis,
  ApexGrid,
} from "ng-apexcharts";

export type ChartOptions = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  fill: ApexFill;
  dataLabels: ApexDataLabels;
  grid: ApexGrid;
  yaxis: ApexYAxis;
  xaxis: ApexXAxis;
  plotOptions: ApexPlotOptions;
};

@Component({
  selector: "app-assessment-projectplan",
  templateUrl: "./assessment-projectplan.component.html",
  styleUrls: ["./assessment-projectplan.component.css"],
})
export class AssessmentProjectplanComponent implements OnInit {
  @ViewChild("chart") chart: ChartComponent;
  public chartOptions: Partial<ChartOptions>;
  addActivityPlanForm: FormGroup;
  addResourcesPlanForm: FormGroup;
  ShowActivityPlanInputBox: boolean = false;
  ShowResourcesInputBox: boolean = false;
  memberListTable: any[];
  projectId: number;
  projectData: projectData;
  weeks: any[] = [];
  startDate: any;
  endDate: any;
  colorIndex = ["#008FFB", "#00E396", "#775DD0", "#FEB019", "#FF4560"];
  activitesDate: any[];
  colorCount: Number = 0;
  private subscription: Subscription = new Subscription();
  excelData1: any;
  excelData2: any;
  displayAlertFile: Boolean = false;
  @ViewChild("fileInput")
  fileInput: ElementRef;
  data :any[]=[];

  constructor(
    private fb: FormBuilder,
    private projectService: ProjectService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private _notificationToast: ToasterNotificatonService
  ) {
    this.activatedRoute.parent.params.subscribe((params) => {
      console.log("see the params", params.id);
      this.projectId = params.id;
    });
  }

  ngOnInit(): void {
    this.addActivityPlanForm = this.fb.group({
      activityPlan: [""],
      activityPlan_week1: [""],
      activityPlan_week2: [""],
      activityPlan_week3: [""],
      activityPlan_week4: [""],
    });

    this.addResourcesPlanForm = this.fb.group({
      resourcesPlan: [""],
      resources_week1: [""],
      resources_week2: [""],
      resources_week3: [""],
      resources_week4: [""],
    });

    this.getProjectById();
  }

  getProjectById() {
    this.projectService.getProjectById(this.projectId).subscribe(
      (data: any) => {
        console.log("see all", data);
        if (data.code === 200) {
          this.projectData = data.responseObject;
          let duration = this.projectData.duration;
          let _duration = duration.split(" ");
          let _durationCount = Number(_duration[0]);
          for (let x = 1; x <= _durationCount; x++) {
            this.weeks.push("week" + x);
          }
          console.log("see the _durationcount", _durationCount);
          console.log("see the weeks", this.weeks);
          this.startDate = new Date(String(this.projectData.startDate));
          this.endDate = new Date(String(this.projectData.endDate));
        } else {
          this._notificationToast.showSuccess(
            `No Project Data For Display`,
            `No Project Data Yet`
          );
        }
      },
      (err) => {
        this._notificationToast.showError(
          `Project Data download failed  `,
          `Download Project Data Failed`
        );
      }
    );
  }

  onFileInput(event: any) {
    console.log("see the file", event.target.files[0]);
    let file = event.target.files[0];
    console.log("see the file", file);
    let fileReader = new FileReader();
    fileReader.readAsBinaryString(file);

    fileReader.onload = (e: any) => {
      let workBook = XLSX.read(fileReader.result, { type: "binary" });

      let sheetNames = workBook.SheetNames;

      this.excelData1 = XLSX.utils.sheet_to_json(
        workBook.Sheets[sheetNames[0]]
      );
      this.excelData2 = XLSX.utils.sheet_to_json(
        workBook.Sheets[sheetNames[1]]
      );
      console.log("see the excel data", this.excelData1);
      console.log("see the excel data", this.excelData2);
      let startdate = this.convertStringToDate(this.excelData1[0].StartDate);

      console.log("see the startdate via function", startdate);
      this.excelData1.forEach((item, index) => {
        if (this.colorIndex.length <= 5) {
          item.styleColor = this.colorIndex[index % this.colorIndex.length];
        }
      });
     

      if (this.excelData1.length === 0) {
        this.displayAlertFile = true;
        this.showAlert1();
      }
      if (!this.displayAlertFile && this.excelData2.length === 0) {
        this.showAlert2();
      }
      //Adding Chart Display Values
      for (let x = 0; x < this.excelData1.length; x++) {
        let chartValues: { [k: string]: any } = {};
        chartValues.x = `${this.excelData1[x].Activities}`;
        chartValues.y = [
          new Date(
            this.convertStringToDate(this.excelData1[x].StartDate)
          ).getTime(),
          new Date(
            this.convertStringToDate(this.excelData1[x].Completed_Date)
          ).getTime(),
        ];
        if (this.colorIndex.length <= 5) {
          chartValues.fillColor =
            this.colorIndex[x % this.colorIndex.length];
        }
        console.log("see the chartValues",chartValues)
        this.data.push(chartValues)
        console.log("see the data array value all",this.data)
        }
        console.log("see the data array value",this.data)
      this.showChart();
      this.reset();
    };
  }

  reset() {
    console.log(this.fileInput.nativeElement.files);
    this.fileInput.nativeElement.value = "";
    console.log(this.fileInput.nativeElement.files);
  }

  showChart() {
    this.chartOptions = {
      series: [
        {
          data: this.data,
        },
      ],
      chart: {
        height: 350,
        type: "rangeBar",
      },
      plotOptions: {
        bar: {
          horizontal: true,
          distributed: true,
          dataLabels: {
            hideOverflowingLabels: true,
          },
        },
      },
      dataLabels: {
        enabled: true,
        formatter: function (val, opts) {
          var label = opts.w.globals.labels[opts.dataPointIndex];
          var a = moment(val[0]);
          var b = moment(val[1]);
          var diff = b.diff(a, "days");
          return label + ": " + diff + (diff > 1 ? " days" : " day");
        },
        style: {
          colors: ["#f3f4f5", "#fff"],
        },
      },
      xaxis: {
        type: "datetime",
      },
      yaxis: {
        show: true,
      },
      grid: {
        row: {
          colors: ["#f3f4f5", "#fff"],
          opacity: 1,
        },
      },
    };
  }

  showAlert1() {
    Swal.fire({
      title: "Activities and Weeks Column Information is Missing",
      text: "You won't be able to work with this Excel file",
      icon: "warning",
      // showCancelButton: true,
      confirmButtonColor: "#3085d6",
      // cancelButtonColor: '#d33',
      confirmButtonText: "Ok, Please Import Again!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.displayAlertFile = false;
      }
    });
  }

  showAlert2() {
    Swal.fire({
      title: "Resources and Weeks Column Information is Missing",
      text: "You won't be able to work with this Excel file",
      icon: "warning",
      // showCancelButton: true,
      confirmButtonColor: "#3085d6",
      // cancelButtonColor: '#d33',
      confirmButtonText: "Ok, Please Import Again!",
    }).then((result) => {
      if (result.isConfirmed) {
      }
    });
  }

  get activityPlan() {
    return this.addActivityPlanForm.get("activityPlan");
  }

  get activityPlan_week1() {
    return this.addActivityPlanForm.get("activityPlan_week1");
  }

  get activityPlan_week2() {
    return this.addActivityPlanForm.get("activityPlan_week2");
  }

  get activityPlan_week3() {
    return this.addActivityPlanForm.get("activityPlan_week3");
  }

  get activityPlan_week4() {
    return this.addActivityPlanForm.get("activityPlan_week4");
  }

  get resourcesPlan() {
    return this.addActivityPlanForm.get("resourcesPlan");
  }

  get resources_week1() {
    return this.addActivityPlanForm.get("resources_week1");
  }

  get resources_week2() {
    return this.addActivityPlanForm.get("resources_week2");
  }

  get resources_week3() {
    return this.addActivityPlanForm.get("resources_week3");
  }

  get resources_week4() {
    return this.addActivityPlanForm.get("resources_week4");
  }

  addActivityPlan() {
    this.ShowActivityPlanInputBox = true;
  }
  cancelActivityPlan() {
    this.ShowActivityPlanInputBox = false;
  }

  addResourcesPlan() {
    this.ShowResourcesInputBox = true;
  }

  cancelResourcesPlan() {
    this.ShowResourcesInputBox = false;
  }

  onSaveActivityPlan() {
    if (!this.addActivityPlanForm.valid) {
      this.addActivityPlanForm.markAllAsTouched();
      return;
    }
    let data = {
      teamMembers: [
        {
          ...this.addActivityPlanForm.value,
        },
      ],
    };

    this.subscription.add(
      this.projectService.createTeamMember(data, this.projectId).subscribe(
        (data: any) => {
          console.log("see the data", data);
          if (data.responseCode === 201) {
            this.memberListTable = data.responseObject.teamMembers;
            this.ShowActivityPlanInputBox = false;
            this._notificationToast.showSuccess(
              "Member Created Sucessfully",
              "Created Member"
            );
            this.activityPlan.reset("");
            this.activityPlan_week1.reset("");
            this.activityPlan_week2.reset("");
            this.activityPlan_week3.reset("");
            this.activityPlan_week4.reset("");
          } else {
            this._notificationToast.showSuccess(
              "Member Creation Failed",
              "Cannot Create Member"
            );
          }
        },
        (err) => {
          console.log("see the error", err);
          if (err instanceof HttpErrorResponse) {
            this._notificationToast.showError(
              "Cannot Create Member",
              `${err.error.errorMessage}`
            );
            //this.router.navigate(['login'])
          } else {
            this._notificationToast.showError(
              "Cannot Create Member Now",
              "Cannot Create Member"
            );
          }
        }
      )
    );
  }

  onSaveResourcesPlan() {
    if (!this.addResourcesPlanForm.valid) {
      this.addResourcesPlanForm.markAllAsTouched();
      return;
    }
    let data = {
      teamMembers: [
        {
          ...this.addResourcesPlanForm.value,
        },
      ],
    };

    this.subscription.add(
      this.projectService.createTeamMember(data, this.projectId).subscribe(
        (data: any) => {
          console.log("see the data", data);
          if (data.responseCode === 201) {
            this.memberListTable = data.responseObject.teamMembers;
            this.ShowActivityPlanInputBox = false;
            this._notificationToast.showSuccess(
              "Member Created Sucessfully",
              "Created Member"
            );
            this.resourcesPlan.reset("");
            this.resources_week1.reset("");
            this.resources_week2.reset("");
            this.resources_week3.reset("");
            this.resources_week4.reset("");
          } else {
            this._notificationToast.showSuccess(
              "Member Creation Failed",
              "Cannot Create Member"
            );
          }
        },
        (err) => {
          console.log("see the error", err);
          if (err instanceof HttpErrorResponse) {
            this._notificationToast.showError(
              "Cannot Create Member",
              `${err.error.errorMessage}`
            );
            //this.router.navigate(['login'])
          } else {
            this._notificationToast.showError(
              "Cannot Create Member Now",
              "Cannot Create Member"
            );
          }
        }
      )
    );
  }
 ///Convert String To Date From Excel
  convertStringToDate(excelDate: any) {
    let date = excelDate.slice(0, 10).replace("'", "");
    return date;
  }
}
