import { HttpErrorResponse } from "@angular/common/http";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { Subscription } from "rxjs";
import { ApplicationDataService } from "src/app/Services/application-data.service";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";
import { projectData } from "../project-list-table/project-list-table.component";

@Component({
  selector: "app-add-application-overview-details",
  templateUrl: "./add-application-overview-details.component.html",
  styleUrls: ["./add-application-overview-details.component.css"],
})
export class AddApplicationOverviewDetailsComponent implements OnInit {
  projectId: number;
  isDiasable:boolean = false;
  addAppOverViewRegisterForm: FormGroup;
  applicationId:String;
  appDataId: any;
  private subscription: Subscription = new Subscription();
  constructor(
    private fb: FormBuilder,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService,
    private router: Router,
    private activatedRoute: ActivatedRoute,private dataService:ApplicationDataService
  ) {
    


  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params) => {
      this.projectId = params["id"];
    });

    

    this.addAppOverViewRegisterForm = this.fb.group({
      applicationName: [""],
      description: [""],
      businessUnit: [""],
      businessCritical: [""],
      highAvailability: [""],
      responseTime: [""],
      noOfInteractions: [""],
      contactFirstName: [""],
      contactLastName: [""],
      email: [""],
      contactNumber: [""],
    });
  }

  








  get applicationName() {
    return this.addAppOverViewRegisterForm.get("applicationName");
  }

  get description() {
    return this.addAppOverViewRegisterForm.get("description");
  }
  get businessUnit() {
    return this.addAppOverViewRegisterForm.get("businessUnit");
  }

  get businessCritical() {
    return this.addAppOverViewRegisterForm.get("businessCritical");
  }
  get highAvailability() {
    return this.addAppOverViewRegisterForm.get("highAvailability");
  }
  get responseTime() {
    return this.addAppOverViewRegisterForm.get("responseTime");
  }
  get noOfInteractions() {
    return this.addAppOverViewRegisterForm.get("noOfInteractions");
  }
  get contactFirstName() {
    return this.addAppOverViewRegisterForm.get("contactFirstName");
  }
  get contactLastName() {
    return this.addAppOverViewRegisterForm.get("contactLastName");
  }
  get email() {
    return this.addAppOverViewRegisterForm.get("email");
  }
  get contactNumber() {
    return this.addAppOverViewRegisterForm.get("contactNumber");
  }

  onSubmit() {
    if (!this.addAppOverViewRegisterForm.valid) {
      this.addAppOverViewRegisterForm.markAllAsTouched();
      return;
    }
    this.addAppOverViewRegisterForm.disable()
    this.isDiasable=true;
    this.subscription.add(
      this.projectService
        .createApplicationOverView(this.addAppOverViewRegisterForm.value,this.projectId)
        .subscribe(
          (data: any) => {
            console.log("see the data", data);
            if (data.responseCode === 201) {
              this.applicationId = data.responseObject.applicationId
              this.dataService.setMessage(this.applicationId)
              this._notificationToast.showSuccess(
                "Application Overview Created Sucessfully",
                "Created Application Overview"
              );
              
              this.applicationName.reset("");
              this.description.reset("");
              this.businessUnit.reset("");
              this.businessCritical.reset("");
              this.highAvailability.reset("");
              this.responseTime.reset("");
              this.noOfInteractions.reset("");
              this.contactFirstName.reset("");
              this.contactLastName.reset("");
              this.email.reset("");
              this.contactNumber.reset("");
              this.router.navigate(
                [`/projectdetailsDashboard/${this.projectId}/OverViewDisplayComponent`],
                {
                  relativeTo: this.activatedRoute,
                  queryParams: { applicationId: `${ this.applicationId}` },
                  queryParamsHandling: 'merge',
                  replaceUrl: true
                }
                
              );

             // this.router.navigate(["customerListTable"]);
            } else {
              this._notificationToast.showSuccess(
                "Application Overview Failed",
                "Cannot Create Application Overview"
              );
            }
          },
          (err) => {
            console.log("see the error", err);
            if (err instanceof HttpErrorResponse) {
              this._notificationToast.showError(
                "Cannot Create Application Overview",
                `${err.error.errorMessage}`
              );
              //this.router.navigate(['login'])
            } else {
              this._notificationToast.showError(
                "Cannot Create Application Overview Now",
                "Cannot Create Application Overview"
              );
            }
          }
        )
    );
  }

  

  toAppOverView() {

    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/addAppOverview`],
      {
        relativeTo: this.activatedRoute,
        queryParams: { applicationId: `${ this.applicationId}` },
        queryParamsHandling: 'merge',
        replaceUrl: true
      }
      
    );
    // this.router.navigate(
    //   [`/projectdetailsDashboard/${this.projectId}/addAppOverview`],
    //   { replaceUrl: true }
    // );
  }
}
