import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";
import { projectData } from "../project-list-table/project-list-table.component";

@Component({
  selector: "app-dashboard",
  templateUrl: "./dashboard.component.html",
  styleUrls: ["./dashboard.component.css"],
})
export class DashboardComponent implements OnInit {
  projectListTable: projectData[] = [];
  image: String;
  names = [
    "richard rick",
    "alphonse dave",
    "kilin mbappe",
    "brock lesnae",
    "kane wills",
    "Mark angel",
    "astro boy",
  ]; //
  colorIndex = ["#dc3545", "#ffc107", "#17a2b8", "#007bff", "#28a745"];
  colorValues: { [k: string]: any } = {};
  constructor(
    private projectService: ProjectService,
    private router: Router,
    private _notificationToast: ToasterNotificatonService
  ) {}

  ngOnInit(): void {
    this.getAllProjects();
  }

  getInitials(nameString, i) {
    const fullName = nameString.split(" ");
    const initials = fullName.shift().charAt(0) + fullName.pop().charAt(0);
    //console.log("see the color i", i);
    //console.log("see the color", this.colorIndex[i % this.colorIndex.length]);
    this.colorValues.fillColor = this.colorIndex[i % this.colorIndex.length];

    return initials.toUpperCase();
  }
  getAllProjects() {
    this.projectService.getAllProjects().subscribe(
      (data: any) => {
        console.log("see all Projects", data);
        if (data.code === 200) {
          this.image = "../assets/Images/UNAITO/Icon/calendar.svg";
          let projectListTable = data.responseObject;
          let projectTableLength = projectListTable?.length;
         // console.log("see project table length",projectTableLength)
          if (projectTableLength!== undefined) {
            projectTableLength = projectTableLength - 1;
            let obj = projectListTable[projectTableLength];
            //Bringing the Latest Project To Top
            projectListTable.unshift(obj);
            projectListTable.pop();
            this.projectListTable = projectListTable;
          } else {
            this.projectListTable = [];
            this._notificationToast.showSuccess(
              `No Projects For Display`,
              `No Projects Yet`
            );
          }
        } else {
          this.projectListTable = [];
          this._notificationToast.showSuccess(
            `No Projects For Display`,
            `No Projects Yet`
          );
        }
      },
      (err) => {
        this._notificationToast.showError(
          `Projects download failed  `,
          `Download all Projects Failed`
        );
      }
    );
  }
  //to Display all users
  //https://stackoverflow.com/questions/68820282/angular-converting-names-to-initials-inside-avatar
  replaceUrl() {
    this.router.navigate([`/projectlist`], { replaceUrl: true });
  }
}
