import { Component, OnInit } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { ApplicationDataService } from "src/app/Services/application-data.service";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";

@Component({
  selector: "app-over-view-display-security-compliance",
  templateUrl: "./over-view-display-security-compliance.component.html",
  styleUrls: ["./over-view-display-security-compliance.component.css"],
})
export class OverViewDisplaySecurityComplianceComponent implements OnInit {
  projectId: number;
  applicationId: any;
  securityComplianceId: String;
  vpn: String;
  encryption: String;
  proxyServer: String;
  dataPrivacy: String;

  constructor(
    private fb: FormBuilder,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private dataService: ApplicationDataService
  ) {}

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((params) => {
      this.applicationId = params["applicationId"];
      console.log("see application id", this.applicationId);
    });
    if (this.applicationId !== undefined) {
      this.getApplicationById();
    }
  }
  getApplicationById() {
    this.projectService
      .getApplicationById(this.applicationId)
      .subscribe((data: any) => {
        console.log("see all overview data", data);
        this.securityComplianceId =
          data.responseObject.securityComplianceList[0].securityComplianceId;
        this.applicationId =
          data.responseObject.securityComplianceList[0].applicationId;
        this.vpn = data.responseObject.securityComplianceList[0].vpn;
        this.encryption =
          data.responseObject.securityComplianceList[0].encryption;
        this.proxyServer =
          data.responseObject.securityComplianceList[0].proxyServer;
        this.dataPrivacy =
          data.responseObject.securityComplianceList[0].dataPrivacy;
      });
  }

  toEdit(){
    
  }
}
