import { Component, OnInit } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { ApplicationDataService } from "src/app/Services/application-data.service";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";

@Component({
  selector: "app-servers",
  templateUrl: "./servers.component.html",
  styleUrls: ["./servers.component.css"],
})
export class ServersComponent implements OnInit {
  projectId: number;
  webserverTab: boolean = true;
  applicationServerTab: boolean;
  databaseServerTab: boolean;
  applicationId: Number;
  webServerAdded: Boolean = false;
  appServerAdded: Boolean = false;
  databaseServerAdded: Boolean = false;
  webServerCount:Number=0
  appServerCount:Number=0
  dataServerCount:Number=0
  constructor(
    private fb: FormBuilder,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private dataService: ApplicationDataService
  ) {}

  ngOnInit(): void {
    this.activatedRoute.parent.params.subscribe((params) => {
      console.log("see the params", params.id);
      this.projectId = params.id;
    });

    this.activatedRoute.queryParams.subscribe((params) => {
      this.applicationId = params["applicationId"];
      console.log("see application id", this.applicationId);
    });
    if (this.applicationId !== undefined) {
      console.log("see application id inside if", this.applicationId);
      this.getApplicationById();
    }
  }
  getApplicationById() {
    this.projectService
      .getApplicationById(this.applicationId)
      .subscribe((data: any) => {
        if (data.responseObject.serverDetailList.length>0) {
          this.webServerAdded = true;
          this.appServerAdded = true;
          this.databaseServerAdded = true;
          this.webServerCount = data.responseObject.serverDetailList.length
          this.appServerCount = data.responseObject.serverDetailList.length
          this.dataServerCount = data.responseObject.serverDetailList.length
          // this.router.navigate(
          //   [`/projectdetailsDashboard/${this.projectId}/overViewDisplayWebServer`],
          //   {
          //     relativeTo: this.activatedRoute,
          //     queryParams: { applicationId: `${ this.applicationId}` },
          //     queryParamsHandling: 'merge',
          //     replaceUrl: true
          //   }

          // );
        }
      });
  }

  webServer() {
    this.webserverTab = true;
    this.applicationServerTab = false;
    this.databaseServerTab = false;
  }
  applicationServer() {
    this.webserverTab = false;
    this.applicationServerTab = true;
    this.databaseServerTab = false;
  }

  databaseServer() {
    this.webserverTab = false;
    this.applicationServerTab = false;
    this.databaseServerTab = true;
  }
}
