import { Component, OnInit } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { ApplicationDataService } from "src/app/Services/application-data.service";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";

@Component({
  selector: "app-over-view-display-application-layer",
  templateUrl: "./over-view-display-application-layer.component.html",
  styleUrls: ["./over-view-display-application-layer.component.css"],
})
export class OverViewDisplayApplicationLayerComponent implements OnInit {
  projectId: number;
  applicationId: any;

  applicationLayerId: String;
  currentApplicationTechnology: String;
  technologyUsed: String;
  architecture: String;
  functionalities: String;
  reArchitectures: String;
  futureStateReArchitecture: String;
  newFunctionality: String;
  futureFunctionality: String;

  constructor(
    private fb: FormBuilder,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private dataService: ApplicationDataService
  ) {}

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((params) => {
      this.applicationId = params["applicationId"];
      console.log("see application id", this.applicationId);
    });
    if (this.applicationId !== undefined) {
      this.getApplicationById();
    }
  }

  getApplicationById() {
    this.projectService
      .getApplicationById(this.applicationId)
      .subscribe((data: any) => {
        console.log("see all overview data", data);
        this.applicationLayerId =
          data.responseObject.applicationLayerList[0].applicationLayerId;
        this.applicationId =
          data.responseObject.applicationLayerList[0].applicationId;
        this.currentApplicationTechnology =
          data.responseObject.applicationLayerList[0].currentApplicationTechnology;
        this.technologyUsed =
          data.responseObject.applicationLayerList[0].technologyUsed;
        this.architecture =
          data.responseObject.applicationLayerList[0].architecture;
        this.functionalities =
          data.responseObject.applicationLayerList[0].functionalities;
        this.reArchitectures =
          data.responseObject.applicationLayerList[0].reArchitectures;
        this.futureStateReArchitecture =
          data.responseObject.applicationLayerList[0].futureStateReArchitecture;
        this.newFunctionality =
          data.responseObject.applicationLayerList[0].newFunctionality;
        this.futureFunctionality =
          data.responseObject.applicationLayerList[0].futureFunctionality;
      });
  }
  toEdit(){
    
  }
}
