import { HttpErrorResponse } from "@angular/common/http";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { Subscription } from "rxjs";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";

@Component({
  selector: "app-add-secur-compi",
  templateUrl: "./add-secur-compi.component.html",
  styleUrls: ["./add-secur-compi.component.css"],
})
export class AddSecurCompiComponent implements OnInit {
  projectId: number;
  applicationId:String;
  addSecurityComplianceForm: FormGroup;
  private subscription: Subscription = new Subscription();
  hippa: String;
  gdpr: String;
  pci: String;
  iso: String;
  other: String;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService
  ) {}

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params) => {
      this.projectId = params["id"];
    });
    this.activatedRoute.queryParams.subscribe(params => {
      this.applicationId = params['applicationId'];
      console.log("see application id",this.applicationId )
      
  });

    this.addSecurityComplianceForm = this.fb.group({
      vpn: [""],
      encryption: [""],
      proxyServer: [""],
      dataPrivacy: [""],
      HIPAA: [""],
      GDPR: [""],
      PCI: [""],
      ISO: [""],
      OTHERS: [""],
      Others_Text: [""],
    });
  }

  get vpn() {
    return this.addSecurityComplianceForm.get("vpn");
  }

  get encryption() {
    return this.addSecurityComplianceForm.get("encryption");
  }
  get proxyServer() {
    return this.addSecurityComplianceForm.get("proxyServer");
  }

  get dataPrivacy() {
    return this.addSecurityComplianceForm.get("dataPrivacy");
  }
  get Others_Text() {
    return this.addSecurityComplianceForm.get("Others_Text");
  }

  GetStats6(event: Event, isChecked: any) {
    if (isChecked) {
      this.hippa = (event.target as HTMLInputElement).value;
    } else {
      this.hippa = null;
    }
  }
  GetStats7(event: Event, isChecked: any) {
    if (isChecked) {
      this.gdpr = (event.target as HTMLInputElement).value;
    } else {
      this.gdpr = null;
    }
  }
  GetStats8(event: Event, isChecked: any) {
    if (isChecked) {
      this.pci = (event.target as HTMLInputElement).value;
    } else {
      this.pci = null;
    }
  }
  GetStats9(event: Event, isChecked: any) {
    if (isChecked) {
      this.iso = (event.target as HTMLInputElement).value;
    } else {
      this.iso = null;
    }
  }
  GetStats10(event: Event, isChecked: any) {
    if (isChecked) {
      this.other = (event.target as HTMLInputElement).value;
    } else {
      this.other = null;
      this.addSecurityComplianceForm.get("Others_Text").setValue("")
    }
  }

  onSubmit() {
    if (!this.addSecurityComplianceForm.valid) {
      this.addSecurityComplianceForm.markAllAsTouched();
      return;
    }
let  data_Privacy;
data_Privacy = `${this.hippa? this.hippa:""} ${this.gdpr? this.gdpr : ""} ${this.pci? this.pci : ""} ${this.iso? this.iso : ""} ${this.addSecurityComplianceForm.get("Others_Text").value}`
this.addSecurityComplianceForm
      .get("dataPrivacy")
      .setValue(
        data_Privacy
      );

    this.subscription.add(
      this.projectService
        .addSecurityCompliance(
          this.addSecurityComplianceForm.value,
          this.applicationId
        )
        .subscribe(
          (data: any) => {
            console.log("see the data", data);
            if (data.responseCode === 201) {
              this._notificationToast.showSuccess(
                "Application Layer Created Sucessfully",
                "Created Application Layer"
              );
              //this.addSecurityComplianceForm.disable()
              // this.vpn.reset("");
              // this.encryption.reset("");
              // this.proxyServer.reset("");
              // this.dataPrivacy.reset("");

              //this.registerForm.controls['roleName'].setValue('0');
              //this.router.navigate(["userListTable"]);
              this.router.navigate(
                [`/projectdetailsDashboard/${this.projectId}/overViewDisplaysecurityCompliance`],
                {
                  relativeTo: this.activatedRoute,
                  queryParams: { applicationId: `${ this.applicationId}` },
                  queryParamsHandling: 'merge',
                  replaceUrl: true
                }
                
              );
            } else {
              this._notificationToast.showSuccess(
                "Application Layer Creation Failed",
                "Cannot Create Application Layer"
              );
            }
          },
          (err) => {
            console.log("see the error", err);
            if (err instanceof HttpErrorResponse) {
              this._notificationToast.showError(
                "Cannot Create Application Layer",
                `${err.error.errorMessage}`
              );
              //this.router.navigate(['login'])
            } else {
              this._notificationToast.showError(
                "Cannot Create Application Layer Now",
                "Cannot Create Application Layer"
              );
            }
          }
        )
    );
  }

  toSecurityCompliance() {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/securityCompliance`],
      {
        relativeTo: this.activatedRoute,
        queryParams: { applicationId: `${ this.applicationId}` },
        queryParamsHandling: 'merge',
        replaceUrl: true
      }
    )
  }
}
