import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ProjectService } from 'src/app/Services/project.service';
import { ToasterNotificatonService } from 'src/app/Services/toaster.notificaton.service';
import { projectData } from '../project-list-table/project-list-table.component';

@Component({
  selector: 'app-project-over-view',
  templateUrl: './project-over-view.component.html',
  styleUrls: ['./project-over-view.component.css']
})
export class ProjectOverViewComponent implements OnInit {
  projectId: number;
  projectData:projectData;
  constructor(private activatedRoute: ActivatedRoute,private projectService: ProjectService, private _notificationToast: ToasterNotificatonService,) {
    this.activatedRoute.parent.params.subscribe(
      (params) => 
      { 
        console.log("see the params",params.id); 
        this.projectId = params.id;
       });
   }

  ngOnInit(): void {
    this.getProjectById( )
  }
  getProjectById() {
    this.projectService.getProjectById(this.projectId).subscribe((data: any) => {
      console.log("see all", data)
      if (data.code === 200) {
        this.projectData = data.responseObject;
      } else {
      
        this._notificationToast.showSuccess(`No Project Data For Display`, `No Project Data Yet`)
      }
    }, (err) => {
      this._notificationToast.showError(`Project Data download failed  `, `Download Project Data Failed`)
    })
       
  }

}
