import { HttpErrorResponse } from "@angular/common/http";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { Subscription } from "rxjs";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";

@Component({
  selector: "app-add-application-layer",
  templateUrl: "./add-application-layer.component.html",
  styleUrls: ["./add-application-layer.component.css"],
})
export class AddApplicationLayerComponent implements OnInit {
  projectId: number;
  applicationId:String;
  addAppLayerForm: FormGroup;
  private subscription: Subscription = new Subscription();

  architectures = [
    {
      id: "Monolith",
      name: "Monolith",
    },
    {
      id: "Microservices",
      name: "Microservices",
    },
    {
      id: "Rest Based",
      name: "Rest Based",
    },
  ];
  constructor(
    private fb: FormBuilder,
    private router: Router,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params) => {
      this.projectId = params["id"];
    });
    this.activatedRoute.queryParams.subscribe(params => {
      this.applicationId = params['applicationId'];
      console.log("see application id",this.applicationId )
      
  });
    this.addAppLayerForm = this.fb.group({
      currentApplicationTechnology: [""],
      technologyUsed: [""],
      architecture: [""],
      functionalities: [""],
      futureStateReArchitecture: [""],
      reArchitectures: [""],
      newFunctionality: [""],
      futureFunctionality: [""],
    });
  }

  get currentApplicationTechnology() {
    return this.addAppLayerForm.get("currentApplicationTechnology");
  }

  get technologyUsed() {
    return this.addAppLayerForm.get("technologyUsed");
  }
  get architecture() {
    return this.addAppLayerForm.get("architecture");
  }

  get functionalities() {
    return this.addAppLayerForm.get("functionalities");
  }
  get reArchitectures() {
    return this.addAppLayerForm.get("reArchitectures");
  }

  get newFunctionality() {
    return this.addAppLayerForm.get("newFunctionality");
  }

  onSubmit() {
    if (!this.addAppLayerForm.valid) {
      this.addAppLayerForm.markAllAsTouched();
      return;
    }
    

    this.subscription.add(
      this.projectService
        .addApplicationLayer(this.addAppLayerForm.value, this.applicationId)
        .subscribe(
          (data: any) => {
            console.log("see the data", data);
            if (data.code === 201) {
              this._notificationToast.showSuccess(
                "Application Layer Created Sucessfully",
                "Created Application Layer"
              );
             // this.addAppLayerForm.disable()
              // this.currentApplicationTechnology.reset("");
              // this.technologyUsed.reset("");
              // this.architecture.reset("");
              // this.functionalities.reset("");
              // //this.futureStateReArchitecture.reset("");
              // this.reArchitectures.reset("");
              // this.newFunctionality.reset("");
              //this.futureFunctionality.reset("");

              //this.registerForm.controls['roleName'].setValue('0');
              //this.router.navigate(["userListTable"]);
              this.router.navigate(
                [`/projectdetailsDashboard/${this.projectId}/overViewDisplayApplicationLayer`],
                {
                  relativeTo: this.activatedRoute,
                  queryParams: { applicationId: `${ this.applicationId}` },
                  queryParamsHandling: 'merge',
                  replaceUrl: true
                }
                
              );
            } else {
              this._notificationToast.showSuccess(
                "Application Layer Creation Failed",
                "Cannot Create Application Layer"
              );
            }
          },
          (err) => {
            console.log("see the error", err);
            if (err instanceof HttpErrorResponse) {
              this._notificationToast.showError(
                "Cannot Create Application Layer",
                `${err.error.errorMessage}`
              );
              //this.router.navigate(['login'])
            } else {
              this._notificationToast.showError(
                "Cannot Create Application Layer Now",
                "Cannot Create Application Layer"
              );
            }
          }
        )
    );
  }

  toApplicationLayer() {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/applicationLayer`],
      {
        relativeTo: this.activatedRoute,
        queryParams: { applicationId: `${ this.applicationId}` },
        queryParamsHandling: 'merge',
        replaceUrl: true
      }
    )
  }
}
