import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ApplicationDataService } from 'src/app/Services/application-data.service';
import { ProjectService } from 'src/app/Services/project.service';
import { ToasterNotificatonService } from 'src/app/Services/toaster.notificaton.service';

@Component({
  selector: 'app-over-view-display-application-server',
  templateUrl: './over-view-display-application-server.component.html',
  styleUrls: ['./over-view-display-application-server.component.css']
})
export class OverViewDisplayApplicationServerComponent implements OnInit {
  projectId: number;
  serverDetailsId: String;
  applicationId: String;
  serverName: String;
  serverType: String;
  makeOrOem: String;
  configuration: String;
  serverCategory: String;
  serverSubCategory: String;
  region: String;
  cpuGpu: String;
  ram: String;
  ipAddress: String;
  dedicatedTotal: String;
  dedicatedAvailable: String;
  sharedTotal: String;
  sharedAvailable: String;
  virtualized: String;
  virtualizationSoftware: String;
  loadBalancer: String;
  loadBalancerDetails: String;
  osName: String;
  osVersion: String;
  serverCompatibility: String;
  osCompatibility: String;
  dbName: String;
  dbVersion: String;
  model: String;
  dbTechCompatibility: String;
  @Input() application_Id: String;

  constructor( private fb: FormBuilder,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private dataService: ApplicationDataService) { }

  ngOnInit(): void {
    if (this.application_Id !== undefined) {
      console.log("see application id inside if application_Id", this.application_Id);
      this.getApplicationById();
    }
  }
  getApplicationById() {
    this.projectService
      .getApplicationById(this.application_Id)
      .subscribe((data: any) => {
        if (data.responseObject.serverDetailList !== undefined) {
          console.log(
            "see the data from overview webserver",
            data.responseObject.serverDetailList[0]
          );
          this.serverDetailsId =
            data.responseObject.serverDetailList[0].serverDetailsId;
          this.applicationId =
            data.responseObject.serverDetailList[0].applicationId;
          this.serverName = data.responseObject.serverDetailList[0].serverName;
          this.serverType = data.responseObject.serverDetailList[0].serverType;
          this.makeOrOem = data.responseObject.serverDetailList[0].makeOrOem;
          this.configuration =
            data.responseObject.serverDetailList[0].configuration;
          this.serverCategory =
            data.responseObject.serverDetailList[0].serverCategory;
          this.serverSubCategory =
            data.responseObject.serverDetailList[0].serverSubCategory;
          this.region = data.responseObject.serverDetailList[0].region;
          this.cpuGpu = data.responseObject.serverDetailList[0].cpuGpu;
          this.ram = data.responseObject.serverDetailList[0].ram;
          this.ipAddress = data.responseObject.serverDetailList[0].ipAddress;
          this.dedicatedTotal =
            data.responseObject.serverDetailList[0].dedicatedTotal;
          this.dedicatedAvailable =
            data.responseObject.serverDetailList[0].dedicatedAvailable;
          this.sharedTotal = data.responseObject.serverDetailList[0].sharedTotal;
          this.sharedAvailable =
            data.responseObject.serverDetailList[0].sharedAvailable;
          this.virtualized = data.responseObject.serverDetailList[0].virtualized;
          this.virtualizationSoftware =
            data.responseObject.serverDetailList[0].virtualizationSoftware;
          this.loadBalancer = data.responseObject.serverDetailList[0].loadBalancer;
          this.loadBalancerDetails =
            data.responseObject.serverDetailList[0].loadBalancerDetails;
          this.osName = data.responseObject.serverDetailList[0].osName;
          this.osVersion = data.responseObject.serverDetailList[0].osVersion;
          this.serverCompatibility =
            data.responseObject.serverDetailList[0].serverCompatibility;
          this.osCompatibility =
            data.responseObject.serverDetailList[0].osCompatibility;
          this.dbName = data.responseObject.serverDetailList[0].dbName;
          this.dbVersion = data.responseObject.serverDetailList[0].dbVersion;
          this.model = data.responseObject.serverDetailList[0].model;
          this.dbTechCompatibility =
            data.responseObject.serverDetailList[0].dbTechCompatibility;
        }
      });
  }
  toEdit() {}
}
