  onSubmit() {
    if (!this.addWebServerForm.valid) {
      this.addWebServerForm.markAllAsTouched();
      return;
    }
    
    // this.subscription.add(this.registerService.create(this.registerForm.get('userName').value,this.registerForm.get('firstName').value,this.registerForm.get('lastName').value,
    // this.registerForm.get('email').value,this.registerForm.get('contactNumber').value,this.registerForm.get('location').value,
    // this.registerForm.get('roleName').value).
    this.addWebServerForm
      .get("dedicatedTotal")
      .setValue(
        this.addWebServerForm.get("dedicatedTotal").value +
          " " +
          this.addWebServerForm.get("dedicatedTotalmem").value
      );
    this.addWebServerForm
      .get("dedicatedAvailable")
      .setValue(
        this.addWebServerForm.get("dedicatedAvailable").value +
          " " +
          this.addWebServerForm.get("dedicatedAvailablemem").value
      );
    this.addWebServerForm
      .get("sharedTotal")
      .setValue(
        this.addWebServerForm.get("sharedTotal").value +
          " " +
          this.addWebServerForm.get("sharedTotalmem").value
      );
    this.addWebServerForm
      .get("sharedAvailable")
      .setValue(
        this.addWebServerForm.get("sharedAvailable").value +
          " " +
          this.addWebServerForm.get("sharedAvailablemem").value
      );
    console.log(
      "see modified value",
      this.addWebServerForm.get("dedicatedTotal").value
    );
    console.log(
      "see modified value",
      this.addWebServerForm.get("dedicatedAvailable").value
    );
    console.log(
      "see modified value",
      this.addWebServerForm.get("sharedTotal").value
    );
    console.log(
      "see modified value",
      this.addWebServerForm.get("sharedAvailable").value
    );
    this.subscription.add(
      this.projectService
        .addWebServerDetails(this.addWebServerForm.value, this.applicationId)
        .subscribe(
          (data: any) => {
            console.log("see the data", data);
            if (data.responseCode === 201) {
              this._notificationToast.showSuccess(
                "Web Server Created Sucessfully",
                "Created Web Server"
              );
              //this.addWebServerForm.disable()
              // this.serverType.reset("");
              // this.makeOrOem.reset("");
              // this.configuration.reset("");
              // this.serverCategory.reset("");
              // this.serverName.reset("");
              // this.serverSubCategory.reset("");
              // this.region.reset("");
              // this.cpuGpu.reset("");
              // this.ram.reset("");
              // this.ipAddress.reset("");
              // this.dedicatedTotal.reset("");
              // this.dedicatedAvailable.reset("");
              // this.sharedTotal.reset("");
              // this.sharedAvailable.reset("");
              // this.virtualized.reset("");
              // this.virtualizationSoftware.reset("");
              // this.loadBalancer.reset("");
              // this.loadBalancerDetails.reset("");
              // this.osName.reset("");
              // this.osVersion.reset("");
              // this.serverCompatibility.reset("");
              // this.osCompatibility.reset("");
              // this.dbName.reset("");
              // this.dbVersion.reset("");
              // this.model.reset("");
              // this.dbTechCompatibility.reset("");
              // this.dedicatedTotalmem.reset("");
              // this.dedicatedAvailablemem.reset("");
              // this.sharedTotalmem.reset("");
              // this.sharedAvailablemem.reset("");

              //this.registerForm.controls['roleName'].setValue('0');
              //this.router.navigate(["userListTable"]);
              this.router.navigate(
                [`/projectdetailsDashboard/${this.projectId}/server`],
                {
                  relativeTo: this.activatedRoute,
                  queryParams: { applicationId: `${ this.applicationId}` },
                  queryParamsHandling: 'merge',
                  replaceUrl: true
                }
                
              );
            } else {
              this._notificationToast.showSuccess(
                "Web Server Creation Failed",
                "Cannot Create Web Server"
              );
            }
          },
          (err) => {
            console.log("see the error", err);
            if (err instanceof HttpErrorResponse) {
              this._notificationToast.showError(
                "Cannot Create Web Server Details",
                `${err.error.errorMessage}`
              );
              //this.router.navigate(['login'])
            } else {
              this._notificationToast.showError(
                "Cannot Create Web Server Details Now",
                "Cannot Create Web Server Details"
              );
            }
          }
        )
    );
  }

