import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2'

@Component({
  selector: 'app-information-validation',
  templateUrl: './information-validation.component.html',
  styleUrls: ['./information-validation.component.css']
})
export class InformationValidationComponent implements OnInit {
  projectId: number;
  showValidation: Boolean;
  constructor(private router: Router, private activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    this.activatedRoute.parent.params.subscribe((params) => {
      console.log("see the params", params.id);
      this.projectId = params.id;
    });

    this.activatedRoute.queryParams.subscribe(params => {
      this.showValidation = params['showValidation'];
      console.log("see show Validation",this.showValidation)
      
  });
  }

  onSendApproval($event)
  {
    Swal.fire({
      title: 'Send For Approval',
      html: '<strong>Are You Sure You Want To Send For Approval ?</strong> <br> If you send you cannot make any changes',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Send'
    }).then((result) => {
      if (result.isConfirmed) {
        this.router.navigate(
          [`/projectdetailsDashboard/${this.projectId}/informationValidation`],
          {
            relativeTo: this.activatedRoute,
            queryParams: { showValidation: 'showSendValidation' },
            queryParamsHandling: 'merge',
            replaceUrl: true
          }
          
        );

      }
    })
  }

}
