import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";
import { projectData } from "../project-list-table/project-list-table.component";
import { DatePipe, formatDate } from "@angular/common";
declare var $: any;
import Swal from "sweetalert2";

@Component({
  selector: "app-project-details",
  templateUrl: "./project-details.component.html",
  styleUrls: ["./project-details.component.css"],
})
export class ProjectDetailsComponent implements OnInit {
  projectId: number;
  projectData: projectData;
  showScopeButton: Boolean = true;
  showDriversButton: Boolean = true;
  hideProjectDetailsDashBoardInf: boolean = false;
  constructor(
    private projectService: ProjectService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private _notificationToast: ToasterNotificatonService,
  ) {
    this.activatedRoute.params.subscribe((params) => {
      this.projectId = params["id"];
    });
  }

  ngOnInit(): void {
    this.getProjectById();
  }

  getProjectById() {
    this.projectService.getProjectById(this.projectId).subscribe(
      (data: any) => {
        console.log("see all", data);
        if (data.code === 200) {
          this.projectData = data.responseObject;
          if (this.projectData.drivers !== null) {
            this.showDriversButton = false;
          }
          if (this.projectData.scope !== null) {
            this.showScopeButton = false;
          }

          if (
            this.projectData.drivers !== null &&
            this.projectData.scope !== null
          ) {
            this.router.navigate(
              [`/projectdetailsDashboard/${this.projectId}/overview`],
              { replaceUrl: true }
            );
          }
        }
      },
      (err) => {
        this._notificationToast.showError(
          `Project Data download failed  `,
          `Download Project Data Failed`
        );
      }
    );
  }
  donotShow() {
    this.hideProjectDetailsDashBoardInf = true;
  }
}
