import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ApplicationDataService } from 'src/app/Services/application-data.service';
import { ProjectService } from 'src/app/Services/project.service';
import { ToasterNotificatonService } from 'src/app/Services/toaster.notificaton.service';

@Component({
  selector: 'app-add-application-overview',
  templateUrl: './add-application-overview.component.html',
  styleUrls: ['./add-application-overview.component.css']
})
export class AddApplicationOverviewComponent implements OnInit {
  projectId: number;
  applicationId:Number;
  appDataId: any;
  constructor(private fb: FormBuilder,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService,
    private router: Router,
    private activatedRoute: ActivatedRoute,private dataService:ApplicationDataService) { }

  ngOnInit(): void {
    this.activatedRoute.parent.params.subscribe((params) => {
      console.log("see the params", params.id);
      this.projectId = params.id;
    });

    this.activatedRoute.queryParams.subscribe(params => {
      this.applicationId = params['applicationId'];
      console.log("see application id",this.applicationId )
      
  });

  this.getProjectById();
  }

  getProjectById() {
    this.projectService.getProjectById(this.projectId).subscribe(
      (data: any) => {
        console.log("see all", data);
        if (data.code === 200) {
          this.appDataId = data?.responseObject?.applicationOverviewList[0]?.applicationId;
          if(this.appDataId!==undefined)
          {
            this.router.navigate(
              [`/projectdetailsDashboard/${this.projectId}/OverViewDisplayComponent`],
              {
                relativeTo: this.activatedRoute,
                queryParams: { applicationId: `${ this.appDataId}` },
                queryParamsHandling: 'merge',
                replaceUrl: true
              }
              
            );
          }
          
      }},
      (err) => {
        this._notificationToast.showError(
          `Project Data download failed  `,
          `Download Project Data Failed`
        );
      }
    );
  }

}
