import { Injectable } from "@angular/core";
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
} from "@angular/common/http";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment";
import { ProjectService } from "../Services/project.service";
import { RegisterService } from "../Services/register.service";

@Injectable()
export class APIKEYInterceptor implements HttpInterceptor {
  constructor(
    private projectService: ProjectService,
    private registerService: RegisterService
  ) {}

  intercept(request: HttpRequest<any>, next: HttpHandler) {
    console.log("see the url from interceptor", request.url);
    console.log("see the  driver string ", this.projectService.addDriversurl);
    if (request.url === environment.API_CREATE_NEWCUSTOMER) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "CUSTOMER-API-KEY"),
      });
      return next.handle(API_KEY_Request);
    } else if (request.url === environment.API_GET_ALL_CUSTOMERS) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "CUSTOMER-API-KEY"),
      });
      return next.handle(API_KEY_Request);
    } else if (request.url === environment.API_GET_CUSTOMER_BY_ID) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "CUSTOMER-API-KEY"),
      });
      return next.handle(API_KEY_Request);
    } else if (request.url === environment.API_CREATE_NEWPROJECT) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      return next.handle(API_KEY_Request);
    } else if (request.url === environment.API_GET_ALL_PROJECTS) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      return next.handle(API_KEY_Request);
    } else if (request.url === this.projectService.projectDetailswithIDLink) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.projectDetailswithIDLink = "";
      return next.handle(API_KEY_Request);
    } else if (request.url === this.projectService.addDriversurl) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.addDriversurl = "";
      return next.handle(API_KEY_Request);
    } else if (request.url === this.projectService.addScopesUrl) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.addScopesUrl = "";
      return next.handle(API_KEY_Request);
    } else if (request.url === this.projectService.addTeamMemberurl) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.addTeamMemberurl = "";
      return next.handle(API_KEY_Request);
    } else if (request.url === this.projectService.addAppOverViewLink) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.addAppOverViewLink = "";
      return next.handle(API_KEY_Request);
    } else if (request.url === this.projectService.appOverviewDisplayLink) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.appOverviewDisplayLink = "";
      return next.handle(API_KEY_Request);
    }
    else if (request.url === this.projectService.addWebServerDetailsLink) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.addWebServerDetailsLink = "";
      return next.handle(API_KEY_Request);
    } else if (request.url === this.projectService.addStorageDetailsLink) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.addStorageDetailsLink = "";
      return next.handle(API_KEY_Request);
    } else if (request.url === this.projectService.addApplicationLayerLink) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.addApplicationLayerLink = "";
      return next.handle(API_KEY_Request);
    }else if (request.url === this.projectService.addIntegrationLayerLink) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.addIntegrationLayerLink = "";
      return next.handle(API_KEY_Request);
    }
    else if (request.url === this.projectService.addSecurityComplianceLayerLink) {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "PROJECT-API-KEY"),
      });
      this.projectService.addSecurityComplianceLayerLink = "";
      return next.handle(API_KEY_Request);
    }
    
    else {
      let API_KEY_Request = request.clone({
        headers: request.headers.append("API-KEY", "USER-API-KEY"),
      });
      return next.handle(API_KEY_Request);
    }
  }
}
