import { HttpErrorResponse } from "@angular/common/http";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { Subscription } from "rxjs";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";

@Component({
  selector: "app-integrations",
  templateUrl: "./integrations.component.html",
  styleUrls: ["./integrations.component.css"],
})
export class IntegrationsComponent implements OnInit {
  addIntegrationsForm: FormGroup;
  ShowInputBox: boolean = false;
  projectId: number;
  applicationId:String;
  integrationListTable: any[];
  private subscription: Subscription = new Subscription();
  vpnReq = [
    {
      id: "Yes",
      name: "Yes",
    },
    {
      id: "No",
      name: "No",
    },
    {
      id: "----",
      name: "----",
    },
  ];

  encryptionReq = [
    {
      id: "Yes",
      name: "Yes",
    },
    {
      id: "No",
      name: "No",
    },
    {
      id: "----",
      name: "----",
    },
  ];
  constructor(
    private fb: FormBuilder,
    private projectService: ProjectService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private _notificationToast: ToasterNotificatonService
  ) {}

  ngOnInit(): void {
    this.activatedRoute.parent.params.subscribe((params) => {
      console.log("see the params", params.id);
      this.projectId = params.id;
    });
    this.activatedRoute.queryParams.subscribe(params => {
      this.applicationId = params['applicationId'];
      console.log("see application id",this.applicationId )
      
  });

    this.addIntegrationsForm = this.fb.group({
      targetApplication: ["", [Validators.required]],
      bandwidth: [""],
      vpn: ["", [Validators.required]],
      encryption: ["", [Validators.required]],
    });

    if (this.applicationId !== undefined) {
      this.getApplicationById();
    }
  }
  getApplicationById() {
    this.projectService
      .getApplicationById(this.applicationId)
      .subscribe((data: any) => {
        console.log("see all overview data", data);
        this.integrationListTable =
          data.responseObject.integrationLayerList;
        console.log("see the Integration Table",this.integrationListTable)
      });
  }


  get targetApplication() {
    return this.addIntegrationsForm.get("targetApplication");
  }

  get bandwidth() {
    return this.addIntegrationsForm.get("bandwidth");
  }

  get vpn() {
    return this.addIntegrationsForm.get("vpn");
  }

  get encryption() {
    return this.addIntegrationsForm.get("encryption");
  }

  addItem() {
    this.ShowInputBox = true;
  }
  cancelItem() {
    this.ShowInputBox = false;
  }

  onSave() {
    if (!this.addIntegrationsForm.valid) {
      this.addIntegrationsForm.markAllAsTouched();
      return;
    }
    // let data = {
    //   teamMembers: [
    //     {
    //       ...this.addTeamMeberForm.value,
    //     },
    //   ],
    // };

    this.subscription.add(
      this.projectService.addIntegrationLayer(this.addIntegrationsForm.value, this.applicationId).subscribe(
        (data: any) => {
          console.log("see the data", data);
          if (data.code === 201) {
            this.integrationListTable = data.responseObject.integrationLayerList;
            this.ShowInputBox = false;
            this._notificationToast.showSuccess(
              "Integration Layer Created Sucessfully",
              "Created Integration Layer"
            );
            // this.targetApplication.reset("");
            // this.bandwidth.reset("");
            // this.vpn.reset("");
            // this.encryption.reset("");
            this.addIntegrationsForm.reset()
            this.getApplicationById();
          }
        },
        (err) => {
          console.log("see the error", err);
          if (err instanceof HttpErrorResponse) {
            this._notificationToast.showError(
              "Cannot Create Integration Layer",
              `${err.error.errorMessage}`
            );
            //this.router.navigate(['login'])
          } else {
            this._notificationToast.showError(
              "Cannot Create Integration Layer Now",
              "Cannot Create Integration Layer"
            );
          }
        }
      )
    );
  }
}
