import { HttpErrorResponse } from "@angular/common/http";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { Subscription } from "rxjs";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";

@Component({
  selector: "app-add-storage",
  templateUrl: "./add-storage.component.html",
  styleUrls: ["./add-storage.component.css"],
})
export class AddStorageComponent implements OnInit {
  projectId: number;
  applicationId:String;
  addStorageForm: FormGroup;
  private subscription: Subscription = new Subscription();
  networkStorageTypes = [
    {
      id: "NetworkAttachedStorageNAS",
      name: "Network attached storage(NAS)-6",
    },
    {
      id: "Cloud_Storage",
      name: "Cloud Storage",
    },
    {
      id: "Private_Cloud_Storage",
      name: "Private Cloud Storage",
    },
  ];
  totMemType = [
    {
      id: "GB",
      name: "GB",
    },
    {
      id: "TB",
      name: "TB",
    },
    {
      id: "MB",
      name: "MB",
    },
  ];
  usedMemType = [
    {
      id: "GB",
      name: "GB",
    },
    {
      id: "TB",
      name: "TB",
    },
    {
      id: "MB",
      name: "MB",
    },
  ];
  avaiMemType = [
    {
      id: "GB",
      name: "GB",
    },
    {
      id: "TB",
      name: "TB",
    },
    {
      id: "MB",
      name: "MB",
    },
  ];
  constructor(
    private fb: FormBuilder,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {
    this.activatedRoute.queryParams.subscribe(params => {
      this.applicationId = params['applicationId'];
      console.log("see application id",this.applicationId )
      
  });
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params) => {
      this.projectId = params["id"];
    });
    this.addStorageForm = this.fb.group({
      totalStorage: [""],
      storageUsed: [""],
      storageAvailable: [""],
      storageType: [""],
      storageDeviceName: [""],
      ipAddress: [""],
      totalMem: [""],
      usedMem: [""],
      availableMem: [""],
      
    });
  }
  get totalStorage() {
    return this.addStorageForm.get("totalStorage");
  }

  get storageUsed() {
    return this.addStorageForm.get("storageUsed");
  }
  get storageAvailable() {
    return this.addStorageForm.get("storageAvailable");
  }

  get storageType() {
    return this.addStorageForm.get("storageType");
  }
  get storageDeviceName() {
    return this.addStorageForm.get("storageDeviceName");
  }

  get ipAddress() {
    return this.addStorageForm.get("ipAddress");
  }
  get totalMem() {
    return this.addStorageForm.get("totalMem");
  }
  get availableMem() {
    return this.addStorageForm.get("availableMem");
  }

  get usedMem() {
    return this.addStorageForm.get("usedMem");
  }

  onSubmit() {
    if (!this.addStorageForm.valid) {
      this.addStorageForm.markAllAsTouched();
      return;
    }
    // this.subscription.add(this.registerService.create(this.registerForm.get('userName').value,this.registerForm.get('firstName').value,this.registerForm.get('lastName').value,
    // this.registerForm.get('email').value,this.registerForm.get('contactNumber').value,this.registerForm.get('location').value,
    // this.registerForm.get('roleName').value).
    this.addStorageForm
      .get("totalStorage")
      .setValue(
        this.addStorageForm.get("totalStorage").value +
          " " +
          this.addStorageForm.get("totalMem").value
      );
    this.addStorageForm
      .get("storageUsed")
      .setValue(
        this.addStorageForm.get("storageUsed").value +
          " " +
          this.addStorageForm.get("usedMem").value
      );
    this.addStorageForm
      .get("storageAvailable")
      .setValue(
        this.addStorageForm.get("storageAvailable").value +
          " " +
          this.addStorageForm.get("availableMem").value
      );

    this.subscription.add(
      this.projectService
        .addStorageDetails(this.addStorageForm.value, this.applicationId)
        .subscribe(
          (data: any) => {
            console.log("see the data", data);
            if (data.code === 201) {
              this._notificationToast.showSuccess(
                "Storage Created Sucessfully",
                "Created Storage"
              );
              //this.addStorageForm.disable()
              // this.totalStorage.reset("");
              // this.storageUsed.reset("");
              // this.storageAvailable.reset("");
              // this.storageType.reset("");
              // this.storageDeviceName.reset("");
              // this.ipAddress.reset("");
              // this.totalMem.reset("");
              // this.usedMem.reset("");
              // this.availableMem.reset("");
              this.router.navigate(
                [`/projectdetailsDashboard/${this.projectId}/overViewDisplaystorage`],
                {
                  relativeTo: this.activatedRoute,
                  queryParams: { applicationId: `${ this.applicationId}` },
                  queryParamsHandling: 'merge',
                  replaceUrl: true
                }
                
              );
             
            } else {
              this._notificationToast.showSuccess(
                "Storage Creation Failed",
                "Cannot Create Storage"
              );
            }
            
            
          },
          (err) => {
            console.log("see the error", err);
            if (err instanceof HttpErrorResponse) {
              this._notificationToast.showError(
                "Cannot Create Storage",
                `${err.error.errorMessage}`
              );
              //this.router.navigate(['login'])
            } else {
              this._notificationToast.showError(
                "Cannot Create  Storage Now",
                "Cannot Create Storage "
              );
            }
          }
        )
    );
    
  }

  toStorage() {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/storage`],
      {
        relativeTo: this.activatedRoute,
        queryParams: { applicationId: `${ this.applicationId}` },
        queryParamsHandling: 'merge',
        replaceUrl: true
      }
    )
  }
}
