import { Component, OnInit } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { ApplicationDataService } from "src/app/Services/application-data.service";
import { ProjectService } from "src/app/Services/project.service";
import { ToasterNotificatonService } from "src/app/Services/toaster.notificaton.service";

@Component({
  selector: "app-over-view-display-storage-server",
  templateUrl: "./over-view-display-storage-server.component.html",
  styleUrls: ["./over-view-display-storage-server.component.css"],
})
export class OverViewDisplayStorageServerComponent implements OnInit {
  storageId: String;
  applicationId: String;
  totalStorage: String;
  storageUsed: String;
  storageAvailable: String;
  storageType: String;
  storageDeviceName: String;
  ipAddress: String;

  constructor(
    private fb: FormBuilder,
    private projectService: ProjectService,
    private _notificationToast: ToasterNotificatonService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private dataService: ApplicationDataService
  ) {}

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((params) => {
      this.applicationId = params["applicationId"];
      console.log("see application id", this.applicationId);
    });
    if (this.applicationId !== undefined) {
      this.getApplicationById();
    }
  }

  getApplicationById() {
    this.projectService
      .getApplicationById(this.applicationId)
      .subscribe((data: any) => {
        console.log("see all overview data", data);
        this.storageId = data.responseObject.storageList[0].storageId;
        this.applicationId = data.responseObject.storageList[0].applicationId;
        this.totalStorage = data.responseObject.storageList[0].totalStorage;
        this.storageUsed = data.responseObject.storageList[0].storageUsed;
        this.storageAvailable =
          data.responseObject.storageList[0].storageAvailable;
        this.storageType = data.responseObject.storageList[0].storageType;
        this.storageDeviceName =
          data.responseObject.storageList[0].storageDeviceName;
        this.ipAddress = data.responseObject.storageList[0].ipAddress;
      });
  }

  toEdit()
  {
    
  }
}
