import { Component, Input, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";

@Component({
  selector: "app-project-details-left-menu",
  templateUrl: "./project-details-left-menu.component.html",
  styleUrls: ["./project-details-left-menu.component.css"],
})
export class ProjectDetailsLeftMenuComponent implements OnInit {
  // @Input() project_id:any
  projectId: number;
  applicationId:Number
  constructor(private router: Router, private activatedRoute: ActivatedRoute) {}

  ngOnInit(): void {
    this.activatedRoute.parent.params.subscribe((params) => {
      console.log("see the params", params.id);
      this.projectId = params.id;
    });
    console.log("see the value transfered for project id", this.projectId);

    this.activatedRoute.queryParams.subscribe(params => {
      this.applicationId = params['applicationId'];
      console.log("see the value transfered for application id",this.applicationId )
      
  });
  
  }

  toOverView() {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/overview`],
      { replaceUrl: true }
    );
  }

  toTeamOnBoard() {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/teamonboard`],
      { replaceUrl: true }
    );
  }

  toAssessmentProjectPlan() {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/assessmentprojectplan`],
      { replaceUrl: true }
    );
  }

  toaddAppOverview() {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/addAppOverview`],
      { replaceUrl: true }
    );
  }

  toServer(applicationId?:any) {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/server`],
      {
        relativeTo: this.activatedRoute,
        queryParams: { applicationId: `${ this.applicationId}` },
        queryParamsHandling: 'merge',
        replaceUrl: true
      }
    );
  }

  toStorage(applicationId?:any) {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/storage`],
      {
        relativeTo: this.activatedRoute,
        queryParams: { applicationId: `${ this.applicationId}` },
        queryParamsHandling: 'merge',
        replaceUrl: true
      }
    );
  }
  toApplicationLayer(applicationId?:any) {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/applicationLayer`],
      {
        relativeTo: this.activatedRoute,
        queryParams: { applicationId: `${ this.applicationId}` },
        queryParamsHandling: 'merge',
        replaceUrl: true
      }
    );
  }
  toAddIntegration(applicationId?:any) {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/addIntegration`],
      {
        relativeTo: this.activatedRoute,
        queryParams: { applicationId: `${ this.applicationId}` },
        queryParamsHandling: 'merge',
        replaceUrl: true
      }
    );
  }
  toSecurityCompliance(applicationId?:any) {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/securityCompliance`],
      {
        relativeTo: this.activatedRoute,
        queryParams: { applicationId: `${ this.applicationId}` },
        queryParamsHandling: 'merge',
        replaceUrl: true
      }
    )
  }
  toPlan()
  {
    
  }
  toinformationValidation() {
    this.router.navigate(
      [`/projectdetailsDashboard/${this.projectId}/informationValidation`],
      { replaceUrl: true }
    );
  }
}
